<template>
  <div>
    <form  @submit.prevent="submit">
      <div class="row justify-content-center">
        <div class="col-sm-9 p-2" style="font-size:12px;">
          <div class="shadow p-3 rounded-lg">
          <!-- INPUT -->
          <div class="sm-form">
            <input
              type="text"
              id="id"
              name="id"
              class="form-control form-control-sm"
              placeholder="id"
              v-model="vdata.id"
              disabled
            />
          </div>
         <div class="row">
             <div class="col-6">
                 <div class="sm-form ">
                     <label for="title">Title gambar</label>
                     <input type="text" id="title" name="title" class="form-control form-control-sm" placeholder="title" v-model="vdata.title" >
                 </div>
                 <div class="sm-form ">
                     <label for="gambar">Gambar 360</label>
                     <input type="file" id="filenya" name="gambar" class="form-control form-control-sm" placeholder="gambar" @change="upload">
                     <input type="text" id="gambar" name="gambar" class="form-control form-control-sm" placeholder="gambar" v-model="vdata.gambar" >
                 </div>
                 <div class="sm-form ">
                     <label for="gambar2">Preview/Thumbnail</label>
                     <input type="file" id="filenya2" name="gambar2" class="form-control form-control-sm" placeholder="gambar2" @change="upload2">
                     <input type="text" id="gambar2" name="gambar2" class="form-control form-control-sm" placeholder="gambar2" v-model="vdata.gambar2" >
                 </div>
                 <p class="p-2">Main : </p>
                 <select class='form-control' v-model="vdata.main">
                 <option>true</option>
                 <option>false</option>
                 </select>
             </div>
             <div class="col-6">
                 <div style="height:100%" class="d-flex justify-content-center align-items-center">
                     <div style="width:100%" class="text-center">
                        <!-- <button type="button" @click="$router.push(`/zadmin/kategori/360data/hotspot?idgambar=${vdata.id}&idpeta=${$route.query.id}&title=${vdata.title}`)" v-if="vdata.id" class="btn btn-sm btn-dark btn-block  ">
                        <img src="https://upload.egov.balangankab.go.id/svg/image.svg" 
                             class="d-inline" style="height:30px;width:20px;filter: brightness(0) invert(1);" alt="">
                        + Hotspot</button> -->
                        <br>
                         <button type="button" @click="$router.push(`/zadmin/kategori/360data/getHotspot?idgambar=${vdata.id}&idpeta=${$route.query.id}&title=${vdata.title}`)" v-if="vdata.id" class="btn btn-sm btn-dark btn-block  ">
                        <img src="https://upload.egov.balangankab.go.id/svg/image.svg" 
                             class="d-inline" style="height:30px;width:20px;filter: brightness(0) invert(1);" alt="">
                        + Get Hotspot</button>
                        <br>
                        <button type="button" @click="$router.push(`/zadmin/kategori/360data/edit?idgambar=${vdata.id}&idpeta=${$route.query.id}`)" v-if="vdata.id" class="btn btn-sm btn-dark btn-block ">
                        <img src="https://upload.egov.balangankab.go.id/svg/news.svg" 
                             class="d-inline" style="height:30px;width:20px;filter: brightness(0) invert(1);" alt="">
                        View 360</button>
                     </div>
                 </div>
             </div>
         </div>
         <hr>
          <!-- END INPUT -->
          <!-- BUTTON PART -->
          <div class="mt-3 p-3">
            <button
              type="submit"
              class="btn btn-sm"
              :class="
                btn == 'tambah'
                  ? 'btn-primary'
                  : btn == 'update'
                  ? 'btn-success'
                  : 'btn-danger'
              "
            >
              <span class="typcn typcn-info"></span> Proses  {{ btn }}
            </button>
            <div class="float-right">
              <button
                type="button"
                v-show="btn!='tambah'"
                @click="
                  btn = 'tambah';
                  vdata = {};
                "
                class="btn btn-sm btn-dark"
              >
                <span class="typcn typcn-edit"></span> Tambah
              </button>
              <button
                type="button"
                @click="btn = 'delete'"
                v-show="btn!='tambah'"
                class="ml-3 btn btn-sm btn-danger"
              >
                <span class="typcn typcn-delete"></span> Delete
              </button>
            </div>
          </div>
          </div>
          <!-- END BUTTON PART -->
        </div>
        <div class="col-sm-9 p-2" style="font-size:12px;">
          <div class="shadow rounded-lg">
            <!-- TABLE PART -->
            <div v-if="datanya.length>0" class="p-2">
             <div class="text-right">
                <vue-json-excel :data="datanya">
                  <button type="button" class="btn btn-sm btn-dark">
                    Export <span class="typcn typcn-chart-area"></span>
                  </button>
                </vue-json-excel>
              </div>
                <Btables :tables="datanya"  :cari="['']" :hide="['gambar','gambar2','point','idpeta']" @selected="ambil" ref="btable" />
            </div>
            <!-- END TABLE PART -->
         </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
// TOTURIAL 
// ganti source autophp, ganti source component btable, ganti path table sdb
import autophp from "@/plugins/autophp_laravel.js"; // ganti source nya
import Btables from "@/components/Btables.vue"; //component btable nya
let urlnya=localStorage.getItem('urlnya');
let sdb = new autophp();
export default {
    layout:'app_admin2',
  components: {
    Btables,
  },
  data() {
    return {
      selected: false,
      vdata: {},
      btn: "tambah",
      datanya:[],
    };
  },
  methods: {
    upload(){
        let file = document.querySelector("#filenya"); // berikan id pada input file
        sdb.collection('tbuser',false,urlnya).upload(file,false,true) // file,compress,scramble
        .then(res=>{
            console.log(res)
            this.vdata.gambar=res;
            this.$forceUpdate();
        })
    },
    upload2(){
        let file = document.querySelector("#filenya2"); // berikan id pada input file
        sdb.collection('tbuser',false,urlnya).upload(file,false,true) // file,compress,scramble
        .then(res=>{
            this.vdata.gambar2=res;
            this.$forceUpdate();
        })
    },
    getData(){
      sdb.collection("app_gis_360data",false,urlnya).doc().select(`select * from app_gis_360data where idpeta='${this.$route.query.id}'`).then(res => {
        this.datanya=res;
        this.$forceUpdate();
      });
    },
    ambil(data) {
      this.vdata = data;
      this.btn = "update";
      this.$forceUpdate();
    },
    async submit() {
      let that=this
      this.vdata.idpeta=this.$route.query.id
      if (confirm("Apakah yakin proses dilanjutkan ?")) {
        if (this.btn == "tambah") {
          delete this.vdata.id;
          await sdb
            .collection("app_gis_360data",true,urlnya)
            .doc()
            .set(this.vdata)
              that.getData();
        } else if (this.btn == "update") {
         await sdb
            .collection("app_gis_360data",true,urlnya)
            .doc()
            .set(this.vdata)
              that.getData();
        } else if (this.btn == "delete") {
          await sdb
            .collection("app_gis_360data",true,urlnya)
            .doc(this.vdata.id)
            .delete()
              that.getData();
        }
      }
    },
  },
  async mounted() {
          this.getData();
  },
};
</script>